.profile__text_name {
    font-weight: 500;
    font-size: 42px;
    line-height: 56px;
    margin-top: 0;
    margin-bottom: 0;
    max-width: 380px;
    height: 43px;
}

@media screen and (max-width: 650px) {
    .profile__text_name {
        font-weight: 500;
        font-size: 22px;
        line-height: 56px;
    }
}

@media screen and (max-width: 500px) {
    .profile__text_name {
        text-align: center;
    }
}
