.page {
    max-width: 100%;
    margin: auto;
    background-color: #000000;
    color: white;
    font-family: "Inter", Arial, sans-serif;
}

@media screen and (max-width: 925px) {
    .page {
        padding: 0 19px;
        display: block;
    }
}

@media (max-width: 300px) {
    .page {
        display: block;
        width: 100%;
    }
}
