.profile__info {
    margin-left: 30px;
    margin-top: 18px;
}

@media screen and (max-width: 650px) {
    .profile__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 0;
    }
}

@media screen and (max-width: 300px) {
    .profile__info {
        margin-top: 14px;
    }
}
