.modal__large-image {
    max-width: 75vw;
    max-height: 75vh;
    background-size: cover;
    margin: auto;
    display: block;
}

@media screen and (max-width: 400px) {
    .modal__large-image {
        max-width: 100%;
        max-height: 100%;
        margin-left: 40px;
        margin-right: 40px;
    }
}
