.profile__photo_edit {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
}
.profile__photo_edit:hover {
    cursor: pointer;
    border: none;
    opacity: 1;
    transition: opacity 0.5s;
    background-image: url("../../../../images/edit-button.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, 0.6);
    background-size: 26px;
}

.profile__photo_edit:focus {
    outline: none;
}
